
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import welcome from "./welcome";
import api from "../../api/index";
export default {
components: {
welcome,
Group,
XInput,
XButton,
Flexbox,
FlexboxItem,
CheckIcon
},
data() {
return {
username: "",
password: "",
checked: false,
showLoading: false
};
},
created() {
const userid = window.localStorage.getItem("user");
const user = window.localStorage.getItem("this is username");
if (userid) {
this.$router.replace("/home");
}
if (user) {
this.username = user;
}
},
methods: {
reset() {
if (this.username == "") {
this.$vux.toast.text("请输入用户名", "middle");
} else {
this.$vux.alert.show({
content: "系统已为您重置了密码，请注意查看手机短信！"
});
}
},
login() {
if (this.username == "") {
this.$vux.toast.text("请输入用户名", "middle");
} else if (this.password == "") {
this.$vux.toast.text("请输入密码", "middle");
} else {
const _this = this;

_this.showLoading = true;

let data = {
username: _this.username,
password: _this.password
};

if (this.checked) {
window.localStorage.setItem("this is username", _this.username);
} else {
window.localStorage.removeItem("this is username");
}

api
.login(data)
.then(res => {
if (res.code === 1) {
_this.showLoading = false;

let date = new Date();
date = date.getTime();
let dateStr = JSON.stringify(date);

window.localStorage.removeItem("user");
window.localStorage.removeItem("username");
window.localStorage.removeItem("time");

window.localStorage.setItem("user", res.data.id);
window.localStorage.setItem("username", res.data.user_login);
window.localStorage.setItem("time", dateStr);

if (_this.$route.query.redirect) {
_this.$router.replace(
decodeURIComponent(_this.$route.query.redirect)
);
} else {
_this.$router.replace("/home");
}
} else {
this.$vux.toast.text(res.msg, "middle");
_this.showLoading = false;
}
})
.catch(error => {});
}
}
}
};



